.baseline {
  position: relative;
}

.baseline:before {
  position: absolute;
  width: auto;
  height: auto;
  z-index: 9999;
  content: '';
  display: block;
  pointer-events: none;
  top: -1px;
  right: 0;
  bottom: 0;
  left: 0;
  background-repeat: repeat;
  background-size: 100% 1.65rem;
  background-position: top left;
  background-image: linear-gradient(rgba(160, 160, 160, 0.1) 0px, transparent 1px, transparent 100%);
}

$ss-font-family: 'Open Sans', Helvetica, Arial, sans-serif;
$serif-font-family: 'lora', Georgia, Times, serif;
$font-ss-bold: 600;
$font-ss-regular: 300;
$font-ss-light: 100;
$font-serif-regular: 300;

html {
  font-size: 16px;
	--base-font-color: var(--theme-color-black);
	height: 100%;
	text-rendering: optimizeLegibility;
}

body {
	@include ms-respond(font-size, 1);
	color: var(--base-font-color);
	background-color: var(--theme-color-white);
  font-family: $ss-font-family;
  font-size: 1em;
  letter-spacing: 0.01em;
  line-height: 1.3333;
}

$modularscale:
(
  base: 10px,
  600px:( base: 10px),
  900px: (base: 14px),
  1300px:(base: 14px),
  1600px:(base: 16px)
);

h1 {
  @include ms-respond(font-size, 4);
  margin-bottom: 0.4em;
  margin-top: 1.25em;
  letter-spacing: -1px;
  font-weight: 700;
  line-height: 1.1;
@media screen and (max-width: 600px) {
  font-size: 32px;
}

}
h2 {
  @include ms-respond(font-size, 2);
  margin-bottom: 1.5em;
  letter-spacing: 0;
  font-weight: 600;
}

h3 {
  @include ms-respond(font-size, 1);
  padding-top: 0.5em;
  margin-bottom: 1em;
  font-weight: $font-ss-bold;
}

h4 {
  @include ms-respond(font-size, 1);
  margin-bottom: 1em;
  font-weight: $font-ss-bold;
}

h5 {
  @include ms-respond(font-size, 1);
  margin-bottom: 1em;
  font-weight: $font-ss-bold;
  text-transform: uppercase;
}

p {
  @include ms-respond(font-size, 1);
  margin-bottom: 1em;
  font-weight: 300;
	a {
		color: var(--theme-color-accent);
	}

  @media screen and (max-width: 600px) {
  font-size: 16px;
}

}

small {
  display: block;
  @include ms-respond(font-size, 1);
  margin-bottom: 1em;
  font-size: 85%;
}

%serif {
  font-family: $serif-font-family;
}

.rte {
  img {
    margin: 1em 0;
  }
	ul {
		margin-top: calc((var(--baseline) - var(--h5)) + (var(--baseline) * 2));
		li {
			line-height: var(--baseline);
			margin-bottom: var(--baseline) / 2;
		}
	}
}

p ~ h2 {
  padding-top: 0.75em;
}