:root {


    --theme-color-h: 264;
    --theme-color-s: 100%;
    --theme-color-l: 50%;
    --theme-color-h-opp: calc(var(--theme-color-h) + 90);

    --theme-color-white: white;
    --theme-color-black: rgb(0, 0, 0);
    --theme-color-grey: #ececec;
    --theme-color-grey-dark: #5e676e;
    --theme-color-accent: hsl(var(--theme-color-h), var(--theme-color-s), var(--theme-color-l));
    --theme-color-accent-dark: hsl(var(--theme-color-h), var(--theme-color-s), 40%);
    --theme-color-accent-light: hsl(var(--theme-color-h), var(--theme-color-s), 60%);
    --theme-color-accent-opposite: hsl(var(--theme-color-h-opp), var(--theme-color-s), 50%);
    --container-max-width: 50rem;
    --header-height: 7.5em;
    --theme-color-nav-links: var(--theme-color-accent);
    --theme-color-nav-background: var(--theme-color-white);
}

@import "lib/modularscale-sass-3.x/stylesheets/modularscale";
@import "lib/utils";
@import "lib/reset.min";
@import "typography";
@import "buttons";
@import "video-grain";
@import "scrollbar";


@import url("https://fonts.googleapis.com/css?family=Work+Sans&display=swap");

html {
    box-sizing: border-box;
    overflow-y: scroll;
}

body {
    margin: 0;
    overflow-x: hidden;
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: var(--theme-color-black);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
label,
input,
text-area,
button,
ul,
li {
    font-family: "Open Sans", Helvetica Neue, Helvetica, sans-serif;
    margin-left: 0;
    margin-right: 0;
    margin-top: 0;
    padding-bottom: 0;
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    color: inherit;
    text-rendering: optimizeLegibility;
}

input,
textarea {
    width: 100%;
    font-size: 1rem;
    padding: 1rem 1.5rem;
    margin-top: 1.3rem;
    display: block;
    font-family: "Open Sans", Helvetica Neue, Helvetica, sans-serif;
    text-rendering: optimizeLegibility;
}



.text-spaced-static {
    font-weight: 100;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
    font-size: 16px;
}
pre {
    background: #001628;
    color: #f5d67b;
    display: flex;
    border-radius: 4px;
    margin-bottom: 2rem;
}

code {
    padding: 2rem;
    font-family: Operator Mono A,Operator Mono B,Source Code Pro,Menlo,Consolas,Monaco,monospace;
}

  
  @media screen and (min-width: 1200px) {
    .project-teaser {
      padding-bottom: 100%;
    }
  }


  .project-teaser:hover .project-teaser__inner {
    transform: rotateX(185deg);
  }

  .project-teaser:hover .teaser-img {
    opacity: 0;
    background: white;
  }

  .project-teaser__inner {
    position: absolute;
    width: 100%;
    height: 100%;
    transform: rotateX(0);
    -webkit-transform-style: preserve-3d;
    -webkit-transition: 0.65s;
  }



  .project-teaser:hover .project-teaser__overlay {
    opacity: 0.8;
    background: var(--theme-color-accent-dark);
  }

  .FLS-1{stroke:lime;stroke-miterlimit:10;stroke-width:6px;}.FLS-2,.FLS-3{fill:none;}.FLS-2{stroke:#999;stroke-width:47px;}.FLS-3{stroke:#e6e6e6;stroke-width:15px;}.FLS-4{fill:#333;}
